*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%;--orange: #e07a5f;--dark-blue: #3d405b;--green: #81b29a;--champagne: #f2cc8f;--eggshell: #f4f1de;--brand-font: "Stalinist One", cursive}@media only screen and (max-width: 56em){html{font-size:56.25%}}@media only screen and (max-width: 35em){html{font-size:50%}}img{display:block;max-width:100%}body{box-sizing:border-box;font-size:1.6rem;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;width:100%;overflow-x:hidden;font-family:"Quicksand", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a:-webkit-any-link{text-decoration:none}.navbar{position:absolute;z-index:500;color:#fff;padding:4rem 5rem}@media only screen and (max-width: 35em){.navbar{padding:3rem}}.navbar__brand{font-size:3.2rem;font-family:"Stalinist One", cursive;font-family:var(--brand-font);letter-spacing:0.15em;color:#fff}@media only screen and (max-width: 35em){.navbar__brand{font-size:2.4rem}}.navbar__brand.dark{color:#333}.navbar__brand.green{color:#227509}.navbar__brand.purple{color:#805ad5}.navbar__brand.red{color:#ee2c2c}.navbar__brand.pink{color:#eb8ec4}.form{min-height:100vh;--r-track-width: 110rem;background-color:#fed7e2;padding:10rem 8rem;display:flex;flex-direction:column;justify-content:center;align-items:center}.form.bg-green{background-color:#e2fed7}@media only screen and (max-width: 35em){.form{padding:10rem 0}}.form__grid{width:100%;max-width:var(--r-track-width);display:grid;grid-template-columns:minmax(25rem, 35rem) 1fr;grid-gap:5rem;padding:5rem 0}@media only screen and (max-width: 750px){.form__grid{grid-template-columns:1fr}}@media only screen and (max-width: 35em){.form__grid{padding:4rem}}@media only screen and (max-width: 25em){.form__grid{padding:2.5rem}}.form__content{display:flex;flex-direction:column;justify-content:center}.form__image{display:flex;align-items:center}@media only screen and (max-width: 750px){.form__image{display:none}}.form__consent{width:100%;max-width:var(--r-track-width);padding:5rem 0}@media only screen and (max-width: 35em){.form__consent{padding:5rem 4rem}}@media only screen and (max-width: 25em){.form__consent{padding:5rem 2.5rem}}.form__consent p{max-width:60rem;line-height:1.7}.form__input:not(:last-child){margin-bottom:2.5rem}.form__input-input{display:block;width:100%;border:none;font-family:inherit;font-weight:500;font-size:1.6rem;padding:1rem 1.5rem;box-shadow:0px 4px 11px 5px rgba(0,0,0,0.1);border-radius:0.4rem;transition:transform 0.3s ease, box-shadow 0.3s ease}.form__input-input:focus{outline:none;transform:translateY(-3px);box-shadow:0px 6px 20px 20px rgba(0,0,0,0.08)}.form__input-label{display:block;font-weight:600;font-size:1.8rem;margin-bottom:1.3rem}@media only screen and (max-width: 35em){.form__input-label{font-size:2.2rem}}.form__input-hint{font-size:1.4rem;margin-top:1rem}.form__btn{--btn-bg: var(--dark-blue);display:block;margin-top:1rem;font-size:1.6rem;cursor:pointer;color:#fff;background-color:var(--btn-bg);padding:1.2rem 2.5rem;border:none;box-shadow:0px 6px 11px 5px rgba(0,0,0,0.1);border-radius:0.4rem}.form__btn.purple{--btn-bg: #805ad5}.form__btn:focus{outline:none}.form__title{margin-bottom:2.4rem;font-size:2.8rem}.form__alt-link{margin-top:1.5rem}.category{min-height:100vh;display:flex;padding:10rem;justify-content:center;background-size:cover;background-position:center}@media only screen and (max-width: 35em){.category{padding:10rem 4rem}}@media only screen and (max-width: 25em){.category{padding:10rem 2.5rem}}.category.age-picker{background-image:url(/static/media/pencils.8f407eaf.jpg)}.category.category-picker{background-image:url(/static/media/color-hands.8a1c0c19.jpg)}.category.media-picker{background-position:bottom;background-image:url(/static/media/monkeys.9e8b69b5.jpg)}.category__container{align-self:center;display:grid;grid-template-columns:repeat(auto-fit, minmax(16rem, 1fr));justify-items:center;grid-gap:4rem;width:100%}.category__container.top{margin-top:5rem;align-self:flex-start}.category__container.bottom{margin-bottom:5rem;align-self:flex-end}.category__btn{display:block;width:100%;max-width:20rem;border:none;color:#fff;font-weight:600;background-color:#e07a5f;background-color:var(--orange);font-size:1.8rem;padding:3rem 4rem;border-radius:1rem;box-shadow:0 1px 3px 0 rgba(0,0,0,0.1),0 1px 2px 0 rgba(0,0,0,0.06);cursor:pointer;transition:transform 0.25s ease}.category__btn:hover,.category__btn:focus{outline:none;transform:translateY(-3px) scale(1.05)}.start{background-image:url(/static/media/background.7314d86a.jpg);background-size:cover;background-position:center;min-height:100vh;display:flex;flex-direction:column;justify-content:flex-end;position:relative;padding:8rem}.start__btn{border:none;display:block;width:-webkit-max-content;width:-moz-max-content;width:max-content;padding:2.5rem 4rem;border-radius:5px;font-size:2.4rem;background-color:#ed8936;color:#fff;font-family:inherit;font-weight:500;cursor:pointer;transition:background 0.3s ease}.start__btn:hover{background-color:#dc6f14}

